* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

h1 {
  text-align: center;
  margin: 40px 0 60px;
  font-size: 36px;
  font-weight: bold;
}

.search-form {
  justify-content: center;
  align-items: center;
  margin-bottom: 60px;
  display: flex;
}

.search-input {
  width: 60%;
  max-width: 400px;
  color: #333;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  box-shadow: 0 0 6px rgba(0, 0, 0, .2);
}

.search-btn {
  color: #fff;
  cursor: pointer;
  background-color: #138065;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 18px;
  box-shadow: 0 0 6px rgba(0, 0, 0, .2);
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  display: flex;
}

.photo-card {
  width: 30%;
  border-radius: 5px;
  margin-bottom: 60px;
  overflow: hidden;
  box-shadow: 0 0 6px rgba(0, 0, 0, .2);
}

.photo-card img {
  width: 100%;
  height: 50%;
  object-fit: cover;
  transition: opacity .2s ease-in-out;
}

.photo-card:hover, .photo-card:focus {
  opacity: .8;
}

.info {
  color: #333;
  text-transform: capitalize;
  padding: 10px;
  font-size: 20px;
  text-decoration: none;
  display: block;
}

.load-more {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #008cba;
  border: none;
  border-radius: 5px;
  margin: 30px auto;
  padding: 10px 20px;
  font-size: 18px;
  display: none;
}

@media screen and (max-width: 768px) {
  .photo-card {
    width: 45%;
  }
}

@media screen and (max-width: 480px) {
  .photo-card {
    width: 100%;
  }
}

